.authContainer{display:flex;justify-content:center;align-items:center;min-height:calc(100vh - 80px);padding:1rem}.authCard{width:100%;max-width:400px;padding:2rem;background-color:var(--card-background);border-radius:var(--radius);box-shadow:var(--shadow)}.authCard h1{margin-bottom:1.5rem;text-align:center;color:var(--primary-color)}.formGroup{margin-bottom:1.25rem}.formGroup label{display:block;margin-bottom:.5rem;font-weight:500}.formGroup input{width:100%;padding:.75rem;border:1px solid var(--border-color);border-radius:var(--radius);font-size:1rem;background-color:#f9fafc}.formGroup input:focus{outline:none;border-color:var(--accent-color);box-shadow:0 0 0 2px rgba(136,192,208,.2)}.submitButton{width:100%;padding:.75rem;background-color:var(--primary-color);color:#fff;border:none;border-radius:var(--radius);font-size:1rem;font-weight:500;transition:background-color .2s ease}.submitButton:hover{background-color:var(--secondary-color)}.submitButton:disabled{background-color:var(--text-lighter);cursor:not-allowed}.error{margin-bottom:1rem;padding:.75rem;background-color:rgba(191,97,106,.1);border-radius:var(--radius);color:var(--error-color);font-size:.9rem}.link{margin-top:1.5rem;text-align:center;font-size:.9rem;color:var(--text-light)}.link a{color:var(--primary-color);font-weight:500}.link a:hover{text-decoration:underline}